.banner {
  color: white;
  object-fit: contain;
  height: 448px;
  width: 100%;

  &__contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;

    &__title {
      font-size: 3rem;
      font-weight: 800;
      border-bottom: 0.3rem;
    }

    &__description {
      width: 45rem;
      line-height: 1.3;
      padding-top: 1rem;
      font-size: 0.8rem;
      max-width: 360px;
      height: 80px;

      
    }

    &__buttons {
      &__button {
        cursor: pointer;
        color: #fff;
        outline: none;
        border: none;
        font-weight: 700;
        border-radius: 0.2vw;
        padding-top: 0.5rem;
        padding-left: 2rem;
        padding-right: 2rem;
        padding-bottom: 0.5rem;
        margin-right: 1rem;
        background-color: rgba(51, 51, 51, 0.5);

        &:hover {
          color: #000;
          background-color: #e6e6e6;
          transition: all 0.2s;
        }
      }
    }
  }

  &__fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(
      180deg,
      transparent,
      rgba(37, 37, 37, 0.61),
      #111
    );
  }
}
